/* ===========================
--- General sections
============================ */

.section {
    min-height: 100vh;
}

.home-section {
    padding-top: 120px;
    padding-bottom: 110px;
    display: block;
    position: relative;

    z-index: 120;
}

.section-heading {
    margin-bottom: 50px;
}

.section-heading h2 {
    font-size: 40px;
    margin-bottom: 10px;
    line-height: 1em;
}

.section-heading i {
    margin-bottom: 20px;
}


/* --- section bg var --- */

.bg-white {
    background: #fff;
}

.bg-gray {
    background: #f8f8f8;
}


.bg-dark {
    background: #575757;
}

/* --- section color var --- */

.text-light {
    color: #fff;
}



/* ===========================
--- Intro
============================ */

.intro {
    width: 100%;
    position: relative;
    background-size: cover;
}

#intro.intro {
    padding: 200px 0 150px 0;
}

.intro .slogan {
    text-align: center;

}

.intro .page-scroll {
    text-align: center;
}


.intro .page-scroll a {
    color: #fff;
}

.brand-heading {
    font-size: 40px;
}

.intro-text {
    font-size: 18px;
}

.intro .slogan h2 {
    color: #FFF;
    text-shadow: none;
    font-size: 60px;
    line-height: 60px;
    font-weight: 700;
    font-family: Montserrat, sans-serif;
    background-color: rgba(0, 0, 0, 0);
    text-decoration: none;
    text-transform: uppercase;
    border-width: 0px;
    border-color: #000;
    border-style: none;
    text-shadow: -1px 0 1px #000;
}

.intro .slogan h4 {
    color: #fff;
    text-shadow: -1px 1px 1px #000;
}


/* ===========================
--- About
============================ */

.icon .fa {
    color: #444;
}

/* ==========================
--- Gallery
============================= */


.item {
    margin: 10px;
}

.item img {
    display: block;
    width: 100%;
    height: auto;
}


/* ===========================
--- Contact
============================ */

.form-wrapper {
    background: #f9f9f9;
    padding: 40px;
    border-radius: 6px;
}


form#contact-form .form-group label {
    text-align: left !important;
    display: block;
    letter-spacing: 1px;
    font-size: 16px;
}

form#contact-form input,
form#contact-form select,
form#contact-form textarea {
    border-radius: 4px;
    border: 1px solid #eee;
    -webkit-box-shadow: none;
    box-shadow: none;
    font-size: 16px;
    margin-bottom: 20px;
    background: #fefefe;
    color: #888;
}

form#contact-form input,
form#contact-form select {
    height: 40px;
}

form#contact-form button {
    border-radius: 4px;
    height: 40px;
    text-transform: none;
    font-size: 16px;
    font-weight: 700;
}

form#contact-form input:focus,
form#contact-form select:focus,
form#contact-form textarea:focus {
    -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, .05);
    box-shadow: 0 1px 2px rgba(0, 0, 0, .05);
    background: #fff;
}

.validation {
    color: red;
    display: none;
    margin: 0 0 20px;
    font-weight: 400;
    font-size: 13px;
}

#sendmessage {
    color: green;
    border: 1px solid green;
    display: none;
    text-align: center;
    padding: 15px;
    font-weight: 600;
    margin-bottom: 15px;
}

#errormessage {
    color: red;
    display: none;
    border: 1px solid red;
    text-align: center;
    padding: 15px;
    font-weight: 600;
    margin-bottom: 15px;
}

#sendmessage.show,
#errormessage.show,
.show {
    display: block;
}

/* ===========================
--- Footer
