    }
}

@media (max-width:480px) {

    .navbar-custom .nav.navbar-nav {
        background-color: rgba(255, 255, 255, .4);
    }

    .navbar.navbar-custom.navbar-fixed-top {
        margin-bottom: 30px;
    }


    .page-scroll a {
        width: 40px;
        height: 40px;
        margin-top: 10px;
        font-size: 20px;
    }

}

